// Fonts
@import url(https://fonts.googleapis.com/css?family=Raleway:300,400,600);
// Variables
// @import "node_modules/bootstrap/scss/bootstrap";
// Bootstrap
@import 'node_modules/dropzone/dist/dropzone';
@import "node_modules/bootstrap/scss/functions";
@import "node_modules/bootstrap/scss/variables";
@import "node_modules/bootstrap/scss/mixins";
// FontAwesome
@import "node_modules/font-awesome/scss/font-awesome";
// Select2
@import 'node_modules/select2/src/scss/core';
.docs__sidebar {
    margin-top: 20px;
}

#app-layout {
    pre {
        background: #000000;
        border-radius: 0;
    }
    // 이하 데일 리즈의 칼라 스킴에서 복사한 스타일시트
    .hljs {
        display: block;
        padding: 0.5em;
        background: #000000;
        color: #BABABA;
    }
    .hljs-comment,
    .hljs-template_comment,
    .hljs-javadoc {
        color: #494B4D;
    }
    .hljs-keyword,
    .ruby .hljs-function .hljs-keyword,
    .hljs-request,
    .hljs-status,
    .nginx .hljs-title {
        color: #F08D24;
    }
    .hljs-function .hljs-keyword,
    .hljs-sub .hljs-keyword,
    .method,
    .hljs-list .hljs-title {
        color: #68C244;
    }
    .hljs-string,
    .hljs-tag .hljs-value,
    .hljs-cdata,
    .hljs-filter .hljs-argument,
    .hljs-attr_selector,
    .apache .hljs-cbracket,
    .hljs-date,
    .tex .hljs-command,
    .coffeescript .hljs-attribute {
        color: #F2D42C;
    }
    .hljs-subst {
        color: #DAEFA3;
    }
    .hljs-regexp {
        color: #E9C062;
    }
    .hljs-title,
    .hljs-sub .hljs-identifier,
    .hljs-pi,
    .hljs-tag,
    .hljs-tag .hljs-keyword,
    .hljs-decorator,
    .hljs-shebang,
    .hljs-prompt {
        color: #E8341C;
    }
    .hljs-symbol,
    .ruby .hljs-symbol .hljs-string,
    .hljs-number {
        color: #8E69C9;
    }
    .hljs-params,
    .hljs-variable,
    .clojure .hljs-attribute {
        color: #1CC3E8;
    }
    .css .hljs-tag,
    .hljs-rules .hljs-property,
    .hljs-pseudo,
    .tex .hljs-special {
        color: #E8341C;
    }
    .css .hljs-class {
        color: #1CC3E8;
    }
    .hljs-rules .hljs-keyword {
        color: #1CC3E8;
    }
    .hljs-rules .hljs-value {
        color: #E8341C;
    }
    .css .hljs-id {
        color: #8B98AB;
    }
    .hljs-annotation,
    .apache .hljs-sqbracket,
    .nginx .hljs-built_in {
        color: #9B859D;
    }
    .hljs-preprocessor,
    .hljs-pragma {
        color: #8996A8;
    }
    .hljs-hexcolor,
    .css .hljs-value .hljs-number {
        color: #DD7B3B;
    }
    .css .hljs-function {
        color: #DAD085;
    }
    .diff .hljs-header,
    .hljs-chunk,
    .tex .hljs-formula {
        background-color: #0E2231;
        color: #F8F8F8;
        font-style: italic;
    }
    .diff .hljs-change {
        background-color: #4A410D;
        color: #F8F8F8;
    }
    .hljs-addition {
        background-color: #253B22;
        color: #F8F8F8;
    }
    .hljs-deletion {
        background-color: #420E09;
        color: #F8F8F8;
    }
    .coffeescript .javascript,
    .javascript .xml,
    .tex .hljs-formula,
    .xml .javascript,
    .xml .vbscript,
    .xml .css,
    .xml .hljs-cdata {
        opacity: 0.5;
    }
    // 이상 데일 리즈의 칼라 스킴에서 복사한 스타일시트
}